// 工单分类
const WorkOrderTypeEnum = [
  { value: 0, label: '项目建议' },
  { value: 1, label: '投诉' },
  { value: 2, label: '其他' }
]
// 工单状态
const WorkOrderStatusEnum = [
  { value: 1, label: '待处理' },
  { value: 2, label: '待确认' },
  { value: 3, label: '已关闭' }
]

export default {
  WorkOrderTypeEnum,
  WorkOrderStatusEnum,
  /* 用户反馈列表 表头数据 */
  userFeedbackColumns: (that) => [
    // {
    //   label: '工单号',
    //   prop: 'FeedbackId',
    //   minWidth: '20%'
    // },
    {
      label: '工单分类',
      prop: 'Sort',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return WorkOrderTypeEnum.find((item) => item.value == row.Sort).label
      }
    },
    {
      label: '问题标题',
      prop: 'FeedbackTitle',
      minWidth: '25%'
    },
    // {
    //   label: '关联项目',
    //   prop: 'RunHourTotal',
    //   minWidth: '20%',
    //   formatter: (row, column, index) => {
    //     return row.LastCreateUserName || '-'
    //   }
    // },
    {
      label: '反馈人',
      prop: 'CreateUserName',
      minWidth: '15%'
    },
    {
      label: '提交时间',
      prop: 'CreateTime',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '工单状态',
      prop: 'Status',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return row.Status ? WorkOrderStatusEnum.find((item) => item.value == row.Status).label : ''
      }
    }
    // {
    //   label: '最后处理人',
    //   prop: 'LastCreateUserName',
    //   minWidth: '20%',
    //   formatter: (row, column, index) => {
    //     return row.LastCreateUserName || '-'
    //   }
    // },
    // {
    //   label: '最后处理时间',
    //   prop: 'LastCreateTime',
    //   minWidth: '20%',
    //   formatter: (row, column, index) => {
    //     return row.RecordCount > 0
    //       ? row.LastCreateTime
    //         ? that.$moment(row.LastCreateTime).format('yyyy-MM-DD HH:mm:ss')
    //         : ''
    //       : '-'
    //   }
    // }
  ]
}



