<template>
  <div>
    <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    />
    <div class="user-feedback-detail">
      <p class="content-title">工单详情</p>
      <el-row
        :class="[
          'feedback-detail-container',
          feedBackInfo.Status === 3 ? 'feedback-detail-container__show' : ''
        ]"
      >
        <el-row class="feedback-detail-container-info">
          <div class="feedback-detail-container-info-title">
            <div>{{ WorkOrderTypeList[feedbackDetail.Sort] }}</div>
            <div class="feedback-detail-title">&nbsp;&nbsp;{{ feedbackDetail.FeedbackTitle }}</div>
            <p class="feedback-detail-status">
              工单状态： <span>{{ WorkOrderStatusList[feedbackDetail.Sort] }}</span>
            </p>
          </div>
          <div class="feedback-detail-container-info-info">
            <el-avatar :src="handlePicUrlOld(feedbackThisHead)" :size="20"></el-avatar>&nbsp;&nbsp;
            {{ feedbackDetail.CreateUserName }} {{ feedbackDetail.CreateUserCompanyName }}
            {{ feedbackDetail.CreateUserPhone }}
            <span class="time">{{ feedbackDetail.CreateTime }}</span>
          </div>

          <div class="feedback-detail-container-info-content">
            {{ feedbackDetail.FeedbackDetail }}
          </div>
          <div
            v-if="!!feedbackDetail.FileUrlArray && feedbackDetail.FileUrlArray.length > 0"
            class="feedback-detail-container-info-pics"
          >
            <el-image
              v-for="(e, i) in feedbackDetail.FileUrlArray"
              :key="i"
              :src="e"
              fit="contain"
              :preview-src-list="feedbackDetail.FileUrlArray"
            ></el-image>
          </div>

          <el-divider></el-divider>
          <div class="feedback-detail-container-info-project">
            <span>工单号：{{ feedbackId }}</span>
            <!-- <span>关联项目：{{ feedbackId }}</span> -->
            <span>联系电话：{{ feedbackDetail.CreateUserPhone }}</span>
            <span>所属企业：{{ feedbackDetail.CreateUserCompanyName }}</span>
          </div>
          <el-divider></el-divider>
        </el-row>
        <el-row class="feedback-detail-container-reply">
          <div
            v-for="(e, i) in feedbackDetail.ReplyList"
            :key="i"
            class="feedback-detail-container-reply-item"
          >
            <div>
              <el-avatar
                :key="e.FeedbackReplyId"
                :src="handlePicUrlOld(e.CreateUserHeadImgUrl)"
                :size="30"
              ></el-avatar>
            </div>
            <div>
              <div>
                {{ e.CreateUserName }}
                <span class="time">
                  {{ formatTime(e.CreateTime).format('yyyy-MM-DD HH:mm:ss') }}
                </span>
              </div>
              <div class="reply-content">
                <el-input
                  resize="none"
                  v-if="!!e.ReplyDetail"
                  readonly
                  width="100%"
                  type="textarea"
                  :value="e.ReplyDetail"
                >
                </el-input>
                <el-image
                  v-else
                  :src="handlePicUrl(e.FileUrl)"
                  :preview-src-list="feedbackDetail.replyPicList"
                  fit="contain"
                ></el-image>
              </div>
            </div>
          </div>
        </el-row>
        <el-divider></el-divider>
        <el-row class="feedback-detail-container-text" type="flex" v-if="feedBackInfo.Status !== 3">
          <el-col :span="21">
            <el-input
              v-if="replyType == 'text'"
              type="textarea"
              placeholder="字数不超过100个字符"
              :maxlength="100"
              v-model="reply"
              rows="4"
            >
            </el-input>
            <!-- <el-input v-else v-model="replyFiles" type="file"></el-input> -->
            <!-- <el-image v-if="replyType != 'text'"></el-image> -->
            <!-- 图片上传相关（本地预览） -->
            <el-upload
              accept=".jpg, .jpeg, .png"
              v-else
              action="#"
              list-type="picture-card"
              :auto-upload="false"
              :limit="picLimit"
              :on-exceed="handleExceed"
              :multiple="true"
              :on-change="onChangePic"
              ref="refUploadPics"
            >
              <i slot="default" class="el-icon-plus"></i>
              <div slot="file" slot-scope="{ file }">
                <img class="el-upload-list__item-thumbnail" :src="file.url" alt="" />
                <span class="el-upload-list__item-actions">
                  <span
                    class="el-upload-list__item-preview"
                    @click="handlePictureCardPreview(file)"
                  >
                    <i class="el-icon-zoom-in"></i>
                  </span>
                  <span
                    v-if="!disabled"
                    class="el-upload-list__item-delete"
                    @click="handleRemove(file)"
                  >
                    <i class="el-icon-delete"></i>
                  </span>
                </span>
              </div>
            </el-upload>
            <el-dialog :modal-append-to-body="false" :visible.sync="dialogVisiblePic">
              <img width="100%" :src="dialogImageUrl" alt="" />
            </el-dialog>
          </el-col>
          <el-col :span="2" :push="1" class="type-btns">
            <el-radio v-model="replyType" label="text">文本</el-radio>
            <el-radio v-model="replyType" label="pic">图片</el-radio>
          </el-col>
        </el-row>
      </el-row>
      <div class="dialog-footer" v-if="feedBackInfo.Status !== 3">
        <el-button @click="handleBackClick">取 消</el-button>
        <el-button type="primary" @click="sendFeedbackReply">发 送</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import _data from './data.js'
import { formatTime } from '@/utils/common'
const { WorkOrderTypeEnum, WorkOrderStatusEnum } = _data

export default {
  name: 'userFeedbackDetail',
  data() {
    return {
      feedbackId: parseInt(this.$route.query.feedbackId),
      feedBackInfo: this.$route.query.feedBackInfo,
      WorkOrderTypeList: WorkOrderTypeEnum.map((item) => item.label),
      WorkOrderStatusList: WorkOrderStatusEnum.map((item) => item.label),
      // 反馈详情
      feedbackDetail: {},
      reply: '',
      replyType: 'text',
      replyFiles: [],
      feedbackThisHead: '',
      // 图片上传相关
      dialogImageUrl: '',
      dialogVisiblePic: false,
      disabled: false,
      picLimit: 3,
      // 图片大小限制 5M
      picSizeLimit: 5 * 1024 * 1024
    }
  },
  mounted() {
    this.checkDetail()
  },
  methods: {
    // 客户反馈查看详情弹窗
    checkDetail() {
      this.feedbackThisHead = this.feedBackInfo.CreateUserHeadImgUrl

      this.$api.monitoringScreenOperation
        .GetGensetProjectFeedback({}, { id: this.feedbackId })
        .then((res) => {
          const data = !!res && !!res.Data && res.Data
          if (data) {
            const feedbackDetail = { ...data }
            const replyPicList = []
            !!feedbackDetail.ReplyList &&
              feedbackDetail.ReplyList.forEach((e) => {
                !!e.FileUrl && !e.ReplyDetail && replyPicList.push(this.handlePicUrl(e.FileUrl))
              })
            !!feedbackDetail.FileUrlArray &&
              (feedbackDetail.FileUrlArray = feedbackDetail.FileUrlArray.map((e) => {
                return this.handlePicUrl(e)
              }))
            feedbackDetail.replyPicList = replyPicList
            // console.log('replyPicList', replyPicList)

            !!feedbackDetail.CreateTime &&
              (feedbackDetail.CreateTime = formatTime(feedbackDetail.CreateTime).format(
                'yyyy-MM-DD HH:mm:ss'
              ))
            this.$set(this, 'feedbackDetail', feedbackDetail)
            // console.log(this.feedbackDetail)
          } else {
            this.mes()
          }
        })
        .catch((err) => {
          this.mes()
          console.error(`ERROR in monitoringScreenOperation get feedback detail data =>`, err)
        })
    },

    // 处理图片URL
    handlePicUrl(url) {
      const res = `${process.env.BASE_API}/${url}`
      return res
    },

    // 处理图片（用户信息在老系统）
    handlePicUrlOld(url) {
      const res = `${process.env.BASE_API}/${url}` // `http://www.fortrustyun.com/${url}`
      return res
    },

    // 反馈详情回复发送
    sendFeedbackReply() {
      const message = this.$message
      const upload = (!!this.$refs.refUploadPics && this.$refs.refUploadPics) || {
        uploadFiles: []
      }
      const uploadFiles = upload.uploadFiles
      if (this.replyType == 'text' && !this.reply) {
        message({
          message: '请输入回复信息！',
          type: 'error'
        })
        return
      } else if (this.replyType != 'text' && uploadFiles.length === 0) {
        message({
          message: '至少上传一张图片！',
          type: 'error'
        })
        return
      }
      this.$confirm('确认发送？')
        .then(() => {
          const loading = this.$loading({
            lock: true,
            text: '数据提交中，请稍候...',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          })
          const params = new FormData()
          params.append('FeedbackId', this.feedbackId)
          params.append('ReplyDetail', this.reply)
          params.append('FileUrl', '')
          // 备注暂无
          params.append('Remark', '')
          const promiseArr = []
          if (uploadFiles.length > 0) {
            uploadFiles.forEach((e, i) => {
              const params = new FormData()
              params.append('FeedbackId', this.feedbackId)
              params.append('ReplyDetail', this.reply)
              params.append('FileUrl', e.raw)
              params.append('Remark', '')
              promiseArr.push(this.sendFeedbackReplyFunc(params))
            })
          } else {
            promiseArr.push(this.sendFeedbackReplyFunc(params))
          }
          Promise.all(promiseArr).then((data) => {
            message({
              message: `发送成功！`,
              type: 'success'
            })
            loading.close()
            // 不关闭
            // this.dialogVisibleFeedback = false
            this.reply = ''
            uploadFiles.length = 0
            this.checkDetail()
          })
        })
        .catch((_) => {})
    },

    // 调用反馈回复接口（单次仅可发送一个图片）
    sendFeedbackReplyFunc(params) {
      return new Promise((resolve, reject) => {
        this.$api.monitoringScreenOperation.AddGGensetProjectFeedbackReply(params).then((res) => {
          if (res.Code === 1) {
            resolve('success')
          } else {
            reject(res)
          }
        })
      })
    },

    formatTime(str) {
      return formatTime(str)
    },

    // 图片上传相关
    handleRemove(file) {
      // upload 对象
      const uid = file.uid
      const upload = this.$refs.refUploadPics
      const uploadFiles = upload.uploadFiles
      try {
        uploadFiles.forEach((e, i) => {
          if (e.uid === uid) uploadFiles.splice(i, 1)
        })
      } catch (error) {
        this.$message({
          type: 'error',
          message: '删除失败！'
        })
        console.error(error)
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisiblePic = true
    },
    handleDownload(file) {},
    handleExceed() {
      this.$message(`仅限上传${this.picLimit}张图片！`)
    },
    onChangePic(file, fileList) {
      const isLt5M = file.size / 1024 <= 5120
      if (!isLt5M) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.warning('上传文件大小不能超过 5MB!')
        return false
      }
    },

    handleBackClick() {
      this.$router.push({ path: '/operation/feedback' })
    }
  }
}
</script>

<style lang="less" scoped>
.user-feedback-detail {
  display: initial !important;
  .feedback-detail-container {
    height: calc(100% - 106px);
    display: flex;
    flex-direction: column;
    // margin-bottom: 16px;
    /deep/ .el-divider--horizontal {
      margin: 8px 0;
      background: #0896dd;
    }
    /deep/.el-textarea__inner {
      outline: 0;
      border-color: #409eff;
    }
    .el-image {
      height: 70px;
      width: 70px;
      &:not(:first-child) {
        margin: 0 0 0 10px;
      }
    }
    &-info {
      &-title {
        display: flex;
        align-items: center;
        & > div:first-child {
          border: 1px solid #0099ff;
          color: #09a4f1;
          padding: 0 4px;
          font-size: 0.8rem;
        }
        & > div.feedback-detail-title {
          font-weight: bold;
          font-size: 1.3rem;
          flex: 1;
        }
        & > p.feedback-detail-status span {
          color: #1abc9c;
        }
      }
      &-info {
        display: flex;
        align-items: center;
        // color: #eee;
        font-size: 0.8rem;
        line-height: 36px;
      }
      &-content {
        font-size: 1rem;
        line-height: 24px;
      }
      &-project {
        display: flex;
        align-items: center;
        justify-items: flex-start;
        span {
          width: 25%;
        }
      }
    }

    &-reply {
      flex: 1;
      // max-height: 250px;
      padding: 5px 0;
      overflow: auto;
      &-item {
        display: flex;
        // margin: 0 0 5px 0;
        & > div:first-child {
          margin: 0 10px 0 10px;
        }
        & > div:last-child {
          & :first-child {
            display: flex;
            align-items: center;
            // color: #eee;
            font-size: 0.8rem;
            margin: 2px 0;
          }
          flex: 1;
          padding: 0 20px 0 0;
        }
      }
    }

    &-text {
      span[aria-hidden] {
        color: #818da5;
      }
      span {
        font-weight: bold;
      }
    }

    &-row {
      display: flex;
      padding: 0 0 20px 0;
      div:nth-child(1) {
        padding: 0 10px 0 0;
        display: flex;
        justify-content: end;
      }
      div:ntd-child(2) {
        width: calc(100% - 80px);
      }
    }
    /deep/ textarea {
      background: #0d1f42;
      color: white;
    }
  }

  .feedback-detail-container__show {
    height: calc(100% - 58px);
  }
  .feedback-detail-container-reply {
    .reply-content {
      width: 65%;
    }
  }
  .feedback-detail-container-text {
    .type-btns {
      .el-radio {
        display: block;
      }
    }
  }
  .dialog-footer {
    text-align: right;
    margin-top: 16px;
    // border-top: 1px solid hsla(0, 0%, 100%, 0.3);
    // padding: 8px 0;
  }
  .time {
    color: #bcbcbc;
    margin-left: 8px !important;
  }
}
</style>



